/* ===>>>>>>>>>. colors */
:root {
  --home-bg-color: antiquewhite;
}
.home {
  position: relative;
  min-height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
}

.intro {
  max-width: 540px;
  /* background-color: var(--home-bg-color); */
  text-align: center;
}

.home-img {
  margin-bottom: 1.5rem;
}

.home-name {
  font-size: var(--h1-font-size);
  font-weight: var(--font-bold);
  margin-bottom: 0.5rem;
}

.home-subheading {
  width: 100%;
}
.home-socials {
  display: flex;
  column-gap: 1.75rem;
  justify-content: center;
  margin: 1.15rem 0;
}

.home-social-link {
  color: var(--title-color);
  font-size: 1.3rem;
  transition: 0.3s ease;
}

.home-social-link:hover {
  color: hsl(43, 100%, 68%);
  transform: translateY(-3px);
}

.scroll-down {
  position: absolute;
  bottom: 2.5rem;
  left: 0;
  width: 100%;
}

.home-scroll-name {
  font-size: var(--small-font-size);
  color: var(--text-color);
}

.mouse {
  border: 2px solid var(--text-color);
  display: block;
  height: 1.6rem;
  width: 1.25rem;
  margin: auto;
  margin-top: 0.75rem;
  border-radius: 1rem;
  position: relative;
}

@keyframes ani-mouse {
  0% {
    top: 15%;
  }
  50% {
    top: 50%;
  }
  100% {
    top: 15%;
  }
}
.wheel {
  background-color: var(--title-color);
  border-radius: 100%;
  width: 0.25rem;
  height: 0.25rem;
  position: absolute;
  top: 0.5rem;
  left: 50%;
  transform: translateX(-50%);
  animation: ani-mouse 1.5s linear infinite;
}

.shapes {
  position: absolute;
  left: 0;
  top: 0;
  height: 100%;
  width: 100%;
  z-index: -1;
}

@keyframes animate-shapes {
  from {
    transform: rotate(0);
  }
  to {
    transform: rotate(360deg);
  }
}

.shape {
  position: absolute;
  animation: animate-shapes 5s linear infinite;
}

.s1 {
  left: 2%;
  top: 10%;
}

.s2 {
  left: 18%;
  top: 30%;
}

.s3 {
  left: 5%;
  bottom: 30%;
}

.s4 {
  left: 2%;
  bottom: 10%;
}

.s5 {
  left: 44%;
  top: 10%;
}

.s6 {
  left: 36%;
  bottom: 10%;
}

.s7 {
  top: 30%;
  left: 75%;
  right: 25%;
}
.s8 {
  right: 24%;
  bottom: 20%;
}
.s9 {
  right: 2%;
  top: 10%;
}
.s10 {
  top: 45%;
  right: 11%;
}

.s11 {
  bottom: 10%;
  right: 2%;
}

@media screen and (max-width: 1024px) {
  .home-social-link {
    font-size: 1.125rem;
  }
}
